  .guest-feature {
	  float: left;
	  margin: 0 $spacing-unit*2 $spacing-unit*2 0;
	  width: 50%;
	  
	  @include media-query($on-tablet) {
		  float: none;
		  margin: 0 0 $spacing-unit*2;
		  width: 100%;
	  }
  }
  
  