/* Blocks
==============================*/

.block {
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	width: 100%;
	
	.wrapper {
		padding-top: $spacing-unit*12;
		padding-bottom: $spacing-unit*12;
		
		
		@include media-query($on-1040){
			padding-top: $spacing-unit*8;
			padding-bottom: $spacing-unit*8;
		}
	}

	.wrapper-A {
		padding-bottom: 0;
	}

	.wrapper-B {
		padding-top: 0;
	}

	.block-left {
		float: left;
		width: 48%;
		
		@include media-query($on-800) {
			float: none;
			margin-bottom: $spacing-unit*4;
			width: 100%;
			
			img {
				max-height: 500px;
			}
		}
	}
	.block-right {
		float: right;
		width: 48%;
		
		@include media-query($on-800) {
			float: none;
			margin-bottom: $spacing-unit*4;
			width: 100%;
			
			img {
				max-height: 500px;
			}
		}
	}
	.title-card {
		padding: 0 0 $spacing-unit*4;
	}
}

.dark-bg {
	color: $white;
}

.light-bg {
	color: $black;

	a {
		color: $primary;

		&:hover,&:focus {
		  color: $black;
		}

		&:visited {
		  color: $primary;
		}
	}
}

/* Home Blocks
------------------------------*/

.batman-at-the-castle {
  position: relative;
  background: linear-gradient(to bottom, $purple-300 0%, $purple-300 59.999999999999%, $purple-500 60%, $purple-300 100%);
  color: $on-secondary;
  text-align: center;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 60%;
    background-image: url('/assets/svg/oh-bats.svg');
    background-repeat: repeat;
    background-position: top left;
    background-size: $spacing-unit * 16;
    z-index: 0;

	@include media-query($on-800){
		height: 40%;
	}
	@include media-query($on-400){
		height: 30%;
	}
  }

  .wrapper {
	padding-top: $spacing-unit*6;
    padding-bottom: $spacing-unit*6;
  }

  > .wrapper,
  h2,
  h4,
  a,
  .small {
    position: relative;
    z-index: 1;
  }

  h2 {
    line-height: 1;
    margin-bottom: 0;
  }

  h4 {
    color: $secondary;
    line-height: 1;
  }

  a {
    display: inline-block;
  }

  .small {
    font-size: 0.8em;
    margin-top: 40px;
  }

  @include media-query($on-800){
	background: linear-gradient(to bottom, $purple-300 0%, $purple-300 39.999999999999%, $purple-500 40%, $purple-300 100%);
  }
  @include media-query($on-400){
	background: linear-gradient(to bottom, $purple-300 0%, $purple-300 29.999999999999%, $purple-500 30%, $purple-300 100%);
  }
}


