.contact {
	
	a {
		color: $secondary;
		text-decoration: none;

		&:hover,&:focus {
			color: $on-primary;
		}
	}
	img {
		display: block;
		max-width: 400px;
		margin: 0 auto $spacing-unit*4;
		width: -webkit-fill-available;
	}
	h2 {
		color: $secondary;
		text-align: center;
	}
	p {
		color: $on-primary;
		margin: 0 auto;
		max-width: 600px;
	}
	#contact-form {
		color: $on-primary;
		margin: $spacing-unit*4 auto 0;
		max-width: 600px;
		width: 100%;

		label {
			display: block;
			font-size: 0.8em;
			margin-bottom: $spacing-unit/2;
		}
		
		input, textarea {
			background: $primary-alt;
			border: 1px solid $white;
			border-radius: $spacing-unit;
			color: $white;
			max-width: calc(100% - 40px);
			
			&::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
				color: $on-primary;
				opacity: 1; /* Firefox */
			}
			&:-ms-input-placeholder { /* Internet Explorer 10-11 */
				color: $on-primary;
			}
			&::-ms-input-placeholder { /* Microsoft Edge */
				color: $on-primary;
			}
		}
		input[type="submit"]{
			background: $secondary;
			border: none;
			border-radius: 0;
			box-shadow: 7px 7px 0 rgba(0,0,0,0.3);
			color: $white;
			font-family: $base-font-family;
  			padding: ($spacing-unit * 1) ($spacing-unit * 2);
  			text-decoration: none;
			text-transform: uppercase;
			width: initial;
			margin: $spacing-unit*4 0 0;
			
			&:hover,
			&:focus {
				background: $black;
			}
		}
	}

	@include media-query($on-800) {
		background-size: 800px;
		background-position: left -400px top -400px, right -400px bottom -400px;

	}

	@include media-query($on-600) {
		background-size: 600px;
		background-position: left -300px top -300px, right -300px bottom -300px;

	}

	@include media-query($on-400) {
		background-size: 400px;
		background-position: left -200px top -200px, right -200px bottom -200px;

	}
}

