.fancy-dress {
	background-color: $primary;
	background-image: url(/assets/svg/cirlces-light.svg);
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	background-attachment: fixed;
	color: $on-secondary;

	@include media-query($on-800) {
		background-size: 800px;
		background-position: left -400px top -400px, right -400px bottom -400px;
	}

	@include media-query($on-600) {
		background-size: 600px;
		background-position: left -300px top -300px, right -300px bottom -300px;
	}

	@include media-query($on-400) {
		background-size: 400px;
		background-position: left -200px top -200px, right -200px bottom -200px;
	}
	
	a {
		color: $secondary;
		text-decoration: none;

		&:hover,&:focus {
			color: $on-primary;
		}
	}
	h2 {
		text-align: center;
		color: $on-primary;
		margin-bottom: 0;
	}
	p {
		color: $on-primary;
	}
	// img {
	// 	display: block;
	// 	margin: 0 auto 40px;
	// 	max-height: 600px;
	// 	width: 100%;
	// }

	.fd-slider {
		margin-top: -20px;
	}

	.fd-image-row {
		display: flex;
		gap: 60px;
		// opacity: 0;
		transform: translateY(50px);
		transition: opacity 0.5s ease, transform 0.5s ease;
		width: 100%; /* Ensure the container takes full width */
		justify-content: center; /* Center the images in the row */
		margin-bottom: 80px;
		overflow: hidden;

		@include media-query($on-800) {
			gap: 20px;
		}

		@include media-query($on-600) {
			gap: 10px;
		}
	}
	
	.fd-image {
		width:200px;
		height: 400px;
		object-fit: contain;
		display: block; /* Ensure images are block elements */

		@include media-query($on-800) {
			width: 150px;
			height: 300px;
		}

		@include media-query($on-600) {
			width: 100px;
			height: 200px;
		}

		@include media-query($on-400) {
			width: 75px;
			height: 150px;
		}
	}
	
	.fd-left {
		transform: translateX(-100vw);
		transition: transform 1s ease-out;
	}
	
	.fd-right {
		transform: translateX(100vw);
		transition: transform 1s ease-out;
	}
	
	.fd-scrolled .fd-left {
		transform: translateX(0);
	}
	
	.fd-scrolled .fd-right {
		transform: translateX(0);
	}
	
	.fd-scrolled .fd-center {
		transform: translateY(0);
	}
	
	.fd-scrolled .fd-image-row {
		opacity: 1;
		transform: translateY(0);
	}

	table {
		border-color: $on-primary;
		color: $on-primary;
		margin: 0 0 $spacing-unit*4;

		th,td {
			background: none;
			border-color: $on-primary;
			color: $on-primary;
		}
		tr:nth-child(even) {
			background: none;
		}
	}
}

